module MindfulYoga.Client.MindfulYoga.View

open Fulma
open Fable.React
open Fable.React.Props
open Feliz
open Feliz.Bulma
open MindfulYoga.Client
open SharedViews

let view =
    div [ Class "mindfulyoga"] [
        emptySection
        figure [ Class "image mobile-only"] [
            img [ Src "img/mindful_bg.jpg"]
        ]
        textSection [
            h1 [] [ str "MINDFUL YOGA "; sup [] [str ""]]
            inP "Mindful yoga (vmav jga) je druh formln meditan praxe."
            inP "Mindful yoga je praktikovn jgy s drazem na prost uvdomovn si toho, co se se mnou v ptomnm okamiku dje."
            inP "Pijmme pocity, emoce, mylenky, bez poteby skutenost jakkoliv mnit. Naslouchme signlm tla a pozorn vnmme kad pohyb. Vytvoen prostor umouje snze uslyet signly tla a pomh nm objevit, co opravdu potebujeme, a u na podloce nebo mimo n."
            inP "Zamen pozornosti na to, co se v danm okamiku dje a jak na probhajc procesy reagujeme, transformuje jgu do formy meditace."
            inP "Vychz z praxe tradin hatha jgy a meditace mindfulness."
            inP "Oteven lekce probhaj v rodinnm centru GaPa Kostelec nad Labem nebo v Dom jgy na Vinohradech."

            Html.p [
                prop.className "cta"
                prop.children [
                    Bulma.buttonLink [
                        button.isPrimary
                        button.isMedium
                        prop.target "_blank"
                        prop.href "https://rezervace.mindfulyoga.cz"
                        prop.text "Rezervovat lekci v Gapku"
                    ]
                    
                    Bulma.buttonLink [
                        button.isPrimary
                        button.isMedium
                        prop.href "https://rezervace.dum-jogy.cz/rs/kalendar_vypis/instruktor/88"
                        prop.text "Rezervovat lekci v Dom jgy"
                    ]
                ]
            ]
            
        ]
        emptySection
        textSection [
            h2 [] [ str "Benefity mindful yogy"]
            [       
                "Zmruje celkov stres"
                "Uvoluje napt vtle"
                "U ns se pijmat a podporovat"
                "Posiluje sebedvru"
                "U ns bt ksob laskav, roste laskavost i kostatnm"
                "Otevr fyzick i duevn prostor nezbytn pro expanzi a rozvoj"
                "Podporuje rst osobn svobody"
                "Je vznamnm nstrojem sebepoznn"
                "A samozejm pin vechny fyzick vhody plynouc zprotahovn a posilovn"
            ] |> inList |> List.singleton |> div []
        ]
        emptySection
        textSection [
            h2 [] [ str "m se takov praxe li od ostatnch?"]
            [       
                "Studenti jsou neustle nabdni kobracen pozornosti ktomu, co provaj vptomnm okamiku."
                "Pozornost vtle zjiujeme, kde se v tle stres projevuje, samotn uvdomovn pomh napt odstranit."
                "Vmme si pocit, emoc, mylenek, smyslovch vjem, ani bychom na n reagovali, pouze zpozice pozorovatele."
                "Zaazovn jednoduchch meditanch technik bhem lekce, dechovch technik na snen stresu."
                "Znovu se napojme na nae tlo a vytvome si prostor pro vnmn jeho signl. asto jsme od svho tla odpojeni a ijeme jen v hlav. Ume
                                    se naslouchat naemu tlu, kde tlo k, e se meme do pozice ponoit hloubji a kde naopak mme zastavit. Pi udren tto pozornosti
                                    se nedostvme do bodu, ve kterm uctme bolest  kad vdom pebr zodpovdnost za ten signl vlastnho tla."

                "Vnmme, kde ns omezuje nae mysl. Kde se vsan nedostaneme dl
                                        ne kvli tlu, ale kde nm brn n vnitn kritik, kter naeptv, e to
                                        nedokeme. Nebo naopak, kde se tlame do maximlnho rozsahu jen
                                        proto, e kolega vedle na podloce se do pozice dostane a tm si
                                        ubliujeme. Prozkoumvme svoje hranice."
                "Postupn odhalujeme a uvdomujeme si mylenkov vzorce, tm mme monost si uvdomit, kter nm u neslou a pemnit je."
                "Vnmme, jak vliv m n duevn a emocionln postoj na n fyzick
                                        postoj. Zmnou fyzickho postoje vdren tla, dochz i kpromn
                                        postoj a pocit."
                "Penme klid a vmavost zjgamatky i do bnho ivota."
            ] |> inList |> List.singleton |> div []
            p [] [
                str "Ve slehkost, radost, zkoumavm zjmem. Pamatujte, pokud se stresujeme
                                bhem praxe jgy, nememe se vbnm ivot zbavit stresu.
                                Jga by nikdy nemla bt brna pli vn."
            ]
        ]
    ]
