module MindfulYoga.Client.MindfulnessRmt.View

open Fulma
open Fable.React
open Fable.React.Props
open Feliz
open Feliz.Bulma
open MindfulYoga.Client
open SharedViews

let view =
    div [ Class "mindfulness-rmt"] [
        emptySection
        figure [ Class "image mobile-only"] [
            img [ Src "img/mindful_bg.jpg"]
        ]
        textSection [
            Html.h1 "Mindfulness"
            
            Html.p [
                Html.text "Mindfulness je druh duevnho trninku, pi kterm rozvjme vmavost s pomoc jednoduchch technik."
                Html.br []
                Html.h3 "Co zskm, kdy se budu vnovat mindfulness?"
            ]
            
            [       
                "Umn t v ptomnosti. Pln vnmm ptomnm okamik. Vtinu asu naeho dne trvme vzpomnnm nebo plnovnm, mezitm nm unik, co se dje prv te. ivot nm unik mezi prsty."
                "Mindfulness funguje jako radar ke vemu, co se uvnit ns dje. Zanm se lpe orientovat ve svch pocitech, emocch, mylenkch. Um se je vnmat s uritm odstupem a akceptovat je msto jejich poprn nebo odmtn. Teprve pijetm situace si vytvm prostor ke zmn nebo ke snadnjmu vypodn se stresovou situac."
                "Umm zvldnout sv nepjemn pocity, kter mi brn zmnit to, co mi nevyhovuje. Zskvm vt svobodu ve svm ivot."
                "Mindfulness funguje jako radar ke vemu, co se dje vn ns. Vmm si malikost a drobnost, kter mi dlaj radost. Drobnosti jsou to, co dl lovka opravdu astnm.  Kolik toho v ivot prost pehldneme?"
            ] |> inList |> List.singleton |> div []
        ]
        emptySection
        textSectionCentered [
            h4 [] [ str "Zani dlat cokoliv, co me nebo o em sn. Smlost m v sob gnia, slu a kouzlo." ]
            p [] [ str "- W.H. Murray"]                            
        ]
        emptySection
        textSection [
            Html.h1 "Relation mindfulness"
            Html.p [
                Html.text "Relation mindfulness se zabv rozvojem vmavosti v mezilidskch vztazch."
                Html.br []
                Html.h3 "Co zskm, kdy se budu vnovat relation mindfulness?"
            ]
            [
                "Naum se pracovat se stresem a vlastnmi emocemi, a to pedevm v momentech, kdy komunikuji nebo spolupracuji s druhmi lidmi."
                "Prohlouben a zlepen vztah. Stvm se laskavj, empatitj a pozornj k lidem v mm okol." 
                "Zlepen orientace sm v sob. Poznvm a uvdomuji si svoje reakce pi komunikaci. Um se pochopit a zmnit ty, kter mi nevyhovuj." 
                "Zvldm konfliktn situace s vt lehkost, naum se nensilnou cestou ci o to, co skuten potebuji. Zlepuji svoje komunikan dovednosti." 
                "Vm, co chci a co ne, snadnji nastavuji hranice vi ostatnm." 
            ] |> inList |> List.singleton |> div []            
            
            Html.h3 "Spolupracuji:"            
            Html.p [
                text.hasTextCentered
                prop.children [
                    Html.img [
                        prop.src "img/mindfulnessclub.png"
                    ]
                ]
            ]
        ]
    ]