type Lesson = {
    Title : string
    Name : string
    Description : string
}

let lessonsGapa = [
    { Title = "Pondl, 18:15  19:25"; Name = "Mindful yoga pro zdrav zda"; Description = "Rozvjen vmavosti bhem klidn plynouc lekce jgy. S ochotou vctit se do svho tla, smujeme pozornost k tomu, co provme v ptomnm okamiku. Vdom dchme.</br></br>Lekce s drazem na zmrnn a prevenci bolest zad. Protaen zkrcench sval trupu a poslen ochablch sval, kter neposkytuj dobrou oporu. Podpora sprvnho dren tla a aktivace hlubokho stabilizanho systmu ptee.</br></br>Jga vhodn pro zatenky, ale i pro pokroil, kte dvaj pednost jemnj praxi. Vhodn i pro seniory a pro vechny se snenou pohyblivost, thotn a po porodu."}
    { Title = "Pondl, 19:40  20:50"; Name = "Mindful flow yoga"; Description = "Probouzme v sob radost a hravost bhem pozdrav slunci. Obas elme njak vzv. Posilujeme sted tla, svaly kolem ptee, uvolujeme kyle.</br></br>Skrze vdom dech uddaj zklidujeme nervov systm, time mysl a detoxikujeme tlo. Ve s plnm respektem k naemu tlu a rozvjejc se vmavost k momentu TADY A TE. Lekce zakonena relaxac.</br></br>Dynamick lekce vhodn pro pokroilej jogny."}
    { Title = "tvrtek, 19:00  20:10"; Name = "Mindful easy flow"; Description = "Jemn plynut na vln dechu, bhem kterho vldn rozproudme energii v celm tle. Zstvme v kontaktu s tm, co se v ns dje a vytvme podmnky pro uvolnn pebytenho napt a stresu.</br></br>Lehce dynamick jga se zamenm na protaen a poslen celho tla, kter zlepuje zdrav orgn a obhovho i dchacho systmu.</br></br>Vhodn pro zatenky i pro pokroilej jogny."}
]

let lessonsDj = [
    { Title = "Steda, 7:30  8:30"; Name = "Hatha jga - Mindful yoga"; Description = "Propojen tradin hatha jgy a praxe mindfulness. Klidn plynut s dechem, bhem kterho postupn probouzme nae tlo a pipravujeme se na nov den. Zstvme v kontaktu s tm, co se v ns dje a pomhme tlu uvolnit pebyten napt a stres. Zanme krtkou meditac a dechovmi technikami, nsleduje jemn protaen a pomal rozpohybovn.<br/>Jga vhodn pro vechny rovn pokroilosti."}
]

let lessonsSection lsns =
    lsns
    |> List.map (fun x -> 
        [
            h2 [] [ str (sprintf "%s / %s" x.Title x.Name)]
            p [ DangerouslySetInnerHTML { __html = x.Description }] [ ]
        ]
    )
    |> List.collect id

let emailForm (model:State) dispatch =
    Html.div [
        Bulma.field [
            Bulma.label "Vae jmno a pjmen *"
            Bulma.control [
                Bulma.textInput [
                    ValidationViews.color model.EmailFormValidationErrors (nameof(model.EmailForm.Name))
                    prop.onTextChange (fun x -> { model.EmailForm with Name = x } |> EmailFormChanged |> dispatch )
                ]
                ValidationViews.help model.EmailFormValidationErrors (nameof(model.EmailForm.Name))
            ]
        ]
        Bulma.field [
            Bulma.label "V email *"
            Bulma.control [
                Bulma.textInput [
                    ValidationViews.color model.EmailFormValidationErrors (nameof(model.EmailForm.Email))
                    prop.onTextChange (fun x -> { model.EmailForm with Email = x } |> EmailFormChanged |> dispatch )
                ]
                ValidationViews.help model.EmailFormValidationErrors (nameof(model.EmailForm.Email))
            ]
        ]
        Bulma.field [
            Bulma.label "Zprva"
            Bulma.control [
                Bulma.textarea [
                    prop.onTextChange (fun x -> { model.EmailForm with Message = x } |> EmailFormChanged |> dispatch )
                ]
            ]
        ]
        Bulma.field [
            Bulma.button [
                button.isPrimary
                button.isFullwidth
                button.isMedium
                prop.text "Pihlsit na lekci"
                prop.onClick (fun _ -> SendEmailForm |> dispatch)
                if model.IsSending then yield! [ button.isLoading; prop.disabled true ]
            ]
        ]
    ]

