module MindfulYoga.Client.IndividualLessons.View

open Fulma
open Fable.React
open Fable.React.Props
open Feliz
open Feliz.Bulma
open MindfulYoga.Client
open SharedViews


let view =
    div [ Class "individual-lessons"] [
        emptySection
        textSection [
            h1 [] [ str "Individuln lekce" ]
            p [] [
                str "Individuln lekce jgy jsou ureny pro vechny, kte z jakhokoliv dvodu potebuj osobn pstup nebo jim cvien ve skupin nevyhovuje. Jsou vhodn i pro ty, kte navtvuj skupinov lekce a chtj se do praxe ponoit hloubji."
                br []
                br []
                str "Lekci pedchz konzultace ohledn zamen lekce, na jejm zklad pipravm koncept cvien na mru. Pi osobnm setkn spolen doladme detaily a zptn (na vydn) Vm zalu pln vetn instrukc ohledn cvien na doma.
                Zskte adu doporuen pro svoji praxi, aby cvien bylo efektivn a pispvalo k Vaemu osobnmu rozvoji."
            ]
        ]
        emptySection
        textSection [
            h3 [] [ str "Mindful Yoga Therapy"]
            p [] [
                str "Individuln lekce "
                a [ Class "mindful-link"; Href Router.MindfulYoga.Path; OnClick Router.goToUrl ] [ str "mindful yogy"]                        
                str " vytvoen na mru s plnm zohlednnm Vaich aktulnch poteb a souasnho zdravotnho stavu."
                br []
                br []
                str "Jgov praxe, bhem kter obracme pozornost ke svmu vnitnmu prosted. Dle cl na poslen a protaen sval, co zlepuje zdrav orgn, kost, obhovho i dchacho systmu."
            ]
            
            h3 [] [ str "Mindful Yin Yoga Therapy"]
            p [] [
                str "Individuln lekce mindful yin yogy vytvoen na mru s plnm zohlednnm Vaich aktulnch poteb a souasnho zdravotnho stavu."
