                br []
                str "Mindful yin yoga pin propojen yin yogy, kter vychz z tradin nsk medicny a mindfulness. Bhem cvien podporujeme rst bdl pozornosti k vnmn svho tla.
                    Praxe, kter je pedevm statick, zahrnuje uvolnn svalovch skupin a zvyovn elasticity/prunosti vaz, kloub a celho tla. Bhem t a ptiminutovch vdr v sanch psobme na hlubok pojivov tkn tj. fascie, vhodn je zatme a tm v nich stimulujeme cirkulaci energie chi."
            ]
                        
            Html.p [
                prop.className "cta"
                prop.children [
                    Bulma.buttonLink [
                        button.isPrimary
                        button.isMedium
                        prop.href Router.Contact.Path
