    div [ Class "retreat"] [
        emptySection
        
        textSection [
            h1 [] [ str "MINDFUL DETOX YOGA RETREAT BOETNSK STATEK "]
            h3 [] [ str "Vkend 13. - 15. listopad 2020" ]
