            Html.p "Boetnsk statek byl postaven v 18. stolet a nedvno byl citliv zrekonstruovn pod taktovkou pan architektky Moniky Krby. Cel staven je propojen s prostornm dvorem a vesnickou zahradou."
            Html.p [
